import React from 'react';
import ReactDOM from 'react-dom';
import { useState } from 'react';
import './popup.css';
import NewsCard from './NewsCard/newsCard';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import SearchIcon from '@mui/icons-material/Search';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
const App = () => {
    let newsCardsRender = [];
    for (var i = 0; i < 3; i++) {
        newsCardsRender.push(React.createElement(NewsCard, { q: "", lang: "en", country: "us", category: "", index: i }));
    }
    const [articles, setArticles] = useState(newsCardsRender);
    const [searchInput, setSearchInput] = useState('');
    const [lang, setLang] = React.useState('');
    const handleChangeLang = (event) => {
        setLang(event.target.value);
    };
    const [country, setCountry] = React.useState('');
    const handleChangeCountry = (event) => {
        setCountry(event.target.value);
    };
    const [category, setCategory] = React.useState('');
    const handleChangeCategory = (event) => {
        setCategory(event.target.value);
    };
    const handleSearchButtonClick = () => {
        newsCardsRender = [];
        for (var i = 0; i < 3; i++) {
            newsCardsRender.push(React.createElement(NewsCard, { q: searchInput, lang: lang, country: country, category: category, index: i }));
        }
        setArticles(newsCardsRender);
    };
    return (React.createElement(Box, { mx: "8px", my: "16px" },
        React.createElement(Paper, { component: "form", sx: {
                p: '2px 4px',
                display: 'flex',
                alignItems: 'center',
                width: 350,
            } },
            React.createElement(InputBase, { sx: { ml: 1, flex: 1 }, placeholder: "Search", value: searchInput, onChange: (event) => setSearchInput(event.target.value) }),
            React.createElement(IconButton, { type: "button", sx: { p: '10px' }, "aria-label": "search", onClick: handleSearchButtonClick },
                React.createElement(SearchIcon, null))),
        React.createElement(FormControl, { sx: { m: 1, minWidth: 105 } },
            React.createElement(InputLabel, { id: "demo-simple-select-label" }, "Lang"),
            React.createElement(Select, { labelId: "demo-simple-select-label", id: "demo-simple-select", value: lang, label: "Language", onChange: handleChangeLang },
                React.createElement(MenuItem, { value: 'ar' }, "Arabic"),
                React.createElement(MenuItem, { value: 'zh' }, "Chinese"),
                React.createElement(MenuItem, { value: 'nl' }, "Dutch"),
                React.createElement(MenuItem, { value: 'en' }, "English"),
                React.createElement(MenuItem, { value: 'fr' }, "French"),
                React.createElement(MenuItem, { value: 'de' }, "German"),
                React.createElement(MenuItem, { value: 'el' }, "Greek"),
                React.createElement(MenuItem, { value: 'hi' }, "Hindi"),
                React.createElement(MenuItem, { value: 'it' }, "Italian"),
                React.createElement(MenuItem, { value: 'ja' }, "Japanese"),
                React.createElement(MenuItem, { value: 'pt' }, "Portuguese"),
                React.createElement(MenuItem, { value: 'ru' }, "Russian"),
                React.createElement(MenuItem, { value: 'es' }, "Spanish"))),
        React.createElement(FormControl, { sx: { m: 1, minWidth: 105 } },
            React.createElement(InputLabel, { id: "demo-simple-select-label" }, "Country"),
            React.createElement(Select, { labelId: "demo-simple-select-label", id: "demo-simple-select", value: country, label: "Language", onChange: handleChangeCountry },
                React.createElement(MenuItem, { value: 'au' }, "Australia"),
                React.createElement(MenuItem, { value: 'br' }, "Brazil"),
                React.createElement(MenuItem, { value: 'ca' }, "Canada"),
                React.createElement(MenuItem, { value: 'cn' }, "China"),
                React.createElement(MenuItem, { value: 'fr' }, "France"),
                React.createElement(MenuItem, { value: 'de' }, "Germany"),
                React.createElement(MenuItem, { value: 'gr' }, "Greece"),
                React.createElement(MenuItem, { value: 'hk' }, "Hong Kong"),
                React.createElement(MenuItem, { value: 'in' }, "India"),
                React.createElement(MenuItem, { value: 'it' }, "Israel"),
                React.createElement(MenuItem, { value: 'jp' }, "Japan"),
                React.createElement(MenuItem, { value: 'pk' }, "Pakistan"),
                React.createElement(MenuItem, { value: 'es' }, "Spain"),
                React.createElement(MenuItem, { value: 'ua' }, "Ukraine"),
                React.createElement(MenuItem, { value: 'uk' }, "United Kingdom"),
                React.createElement(MenuItem, { value: 'us' }, "United States"))),
        React.createElement(FormControl, { sx: { m: 1, minWidth: 125 } },
            React.createElement(InputLabel, { id: "demo-simple-select-label" }, "Category"),
            React.createElement(Select, { labelId: "demo-simple-select-label", id: "demo-simple-select", value: country, label: "Language", onChange: handleChangeCategory },
                React.createElement(MenuItem, { value: 'general' }, "General"),
                React.createElement(MenuItem, { value: 'world' }, "World"),
                React.createElement(MenuItem, { value: 'nation' }, "Nation"),
                React.createElement(MenuItem, { value: 'business' }, "Business"),
                React.createElement(MenuItem, { value: 'technology' }, "Technology"),
                React.createElement(MenuItem, { value: 'entertainment' }, "Entertainment"),
                React.createElement(MenuItem, { value: 'sports' }, "Sports"),
                React.createElement(MenuItem, { value: 'science' }, "Science"),
                React.createElement(MenuItem, { value: 'health' }, "Health"))),
        articles.map((art) => art)));
};
const root = document.createElement('div');
document.body.appendChild(root);
ReactDOM.render(React.createElement(App, null), root);
